import type { CacheEntry, ICacheProvider } from './interface';

const DB_NAME = 'UnifiedCacheDB';
const STORE_NAME = 'cache';
const DB_VERSION = 1;

/**
 * IndexedDB 快取 (IndexedDBCache) 的實作，
 * 底層以 IndexedDB 做資料儲存，keyPath 為 'key'，value 為 CacheEntry 內容
 */
export class IndexedDBCache implements ICacheProvider {
    /**
     * 取得已開啟的 IndexedDB 連線，若是首次開啟會建立 object store
     */
    private openDB(): Promise<IDBDatabase> {
        return new Promise((resolve, reject) => {
            const request = window.indexedDB.open(DB_NAME, DB_VERSION);
            request.onupgradeneeded = (event) => {
                const db = (event.target as IDBOpenDBRequest).result;
                if (!db.objectStoreNames.contains(STORE_NAME)) {
                    db.createObjectStore(STORE_NAME, { keyPath: 'key' });
                }
            };
            request.onsuccess = (event) => {
                const db = (event.target as IDBOpenDBRequest).result;
                resolve(db);
            };
            request.onerror = (event) => {
                reject((event.target as IDBOpenDBRequest).error);
            };
        });
    }

    async get(key: string): Promise<CacheEntry | null> {
        const db = await this.openDB();
        return new Promise((resolve, reject) => {
            const tx = db.transaction(STORE_NAME, 'readonly');
            const store = tx.objectStore(STORE_NAME);
            const req = store.get(key);

            req.onsuccess = (evt) => {
                const record: (CacheEntry & { key: string }) | undefined = (evt.target as IDBRequest).result;
                if (!record) {
                    resolve(null);
                } else {
                    const now = Date.now();
                    // 若過期，自動刪除並回傳 null
                    if (typeof record.timestamp === 'number' && typeof record.expiredMillis === 'number' && now - record.timestamp >= record.expiredMillis) {
                        const deleteTx = db.transaction(STORE_NAME, 'readwrite');
                        deleteTx.objectStore(STORE_NAME).delete(key);
                        resolve(null);
                    } else {
                        resolve({
                            value: record.value,
                            timestamp: record.timestamp,
                            expiredMillis: record.expiredMillis,
                        });
                    }
                }
            };

            req.onerror = (evt) => {
                reject((evt.target as IDBRequest).error);
            };
        });
    }

    async set(key: string, entry: CacheEntry): Promise<void> {
        const db = await this.openDB();
        return new Promise((resolve, reject) => {
            const tx = db.transaction(STORE_NAME, 'readwrite');
            const store = tx.objectStore(STORE_NAME);
            // 直接 put 會存成 { key, value, timestamp, expiredMillis }
            store.put({ key, ...entry });
            tx.oncomplete = () => resolve();
            tx.onerror = (evt) => reject((evt.target as IDBRequest).error);
        });
    }

    async delete(key: string): Promise<void> {
        const db = await this.openDB();
        return new Promise((resolve, reject) => {
            const tx = db.transaction(STORE_NAME, 'readwrite');
            tx.objectStore(STORE_NAME).delete(key);
            tx.oncomplete = () => resolve();
            tx.onerror = (evt) => reject((evt.target as IDBRequest).error);
        });
    }

    async clear(): Promise<void> {
        const db = await this.openDB();
        return new Promise((resolve, reject) => {
            const tx = db.transaction(STORE_NAME, 'readwrite');
            tx.objectStore(STORE_NAME).clear();
            tx.oncomplete = () => resolve();
            tx.onerror = (evt) => reject((evt.target as IDBRequest).error);
        });
    }

    async deleteByPrefix(prefix: string): Promise<void> {
        const db = await this.openDB();
        return new Promise((resolve, reject) => {
            const tx = db.transaction(STORE_NAME, 'readwrite');
            const store = tx.objectStore(STORE_NAME);
            const req = store.openCursor();

            req.onsuccess = (event) => {
                const cursor = (event.target as IDBRequest<IDBCursorWithValue>).result;
                if (cursor) {
                    if (cursor.key.toString().startsWith(prefix)) {
                        cursor.delete();
                    }
                    cursor.continue();
                }
            };

            tx.oncomplete = () => resolve();
            tx.onerror = (event) => reject((event.target as IDBRequest).error);
        });
    }
}
